import React from 'react'

const TicketSummaryViewModal = () => {
  return (
      <div className="modal fade ticketSummaryView" id="ticketSummaryView">
      <div className="modal-dialog modal-dialog-centered">
        <div className="modal-content rounded-0 border-0">
          <div className="modal-header border-0 pb-0">
            <h1 className="modal-title fs-5 fw-bold text-primary">Ticket History</h1>
            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
          </div>
          <div className="modal-body">
            <div className="summaryForm formPanel">
              <strong className="fs-6 d-block text-orange mb-3">Message: Email Password Reset Done, Now
                Ok</strong>
              <div className="form-group mb-3">
                <label htmlFor>Ticket Generated By:</label>
                <input type="text" className="form-control" placeholder="Nikita Prajapat" readOnly />
              </div>
              <div className="row">
                <div className="col-md-6">
                  <div className="form-group mb-3">
                    <label htmlFor>Issue Type</label>
                    <input type="text" className="form-control" placeholder="Email Related" readOnly />
                  </div>
                </div>
                <div className="col-md-6">
                  <div className="form-group mb-3">
                    <label htmlFor>No. of Impacted Persons</label>
                    <input type="text" className="form-control" placeholder={1} readOnly />
                  </div>
                </div>
              </div>
              <div className="form-group mb-3">
                <label htmlFor>Issue Sub-Type</label>
                <input type="text" className="form-control" placeholder="Email Configuration Related" readOnly />
              </div>
              <div className="form-group mb-3">
                <label htmlFor>Issue Subject</label>
                <input type="text" className="form-control" placeholder="Password Reset" readOnly />
              </div>
              <div className="form-group mb-3" id="reOpenTexarea" style={{display: 'none'}}>
                <label>Re-Open Message</label>
                <textarea className="form-control" placeholder="Enter here..." defaultValue={""} />
              </div>
              <div className="text-center">
                <button type="button" className="btn btn-primary gradientBg reOpenToggle">
                  Re-Open Ticket
                </button>
                <button type="button" className="btn btn-org-gradient subMitBtn" style={{display: 'none'}}>
                  Submit
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default TicketSummaryViewModal