import React from 'react'

const TicketHistoryModal = () => {
  return (
      <div className="modal fade ticketHistroyView" id="ticketHistoryModal">
      <div className="modal-dialog modal-dialog-centered">
        <div className="modal-content rounded-0 border-0">
          <div className="modal-header border-0 pb-0">
            <h1 className="modal-title fs-5 fw-bold text-primary">Ticket History</h1>
            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
          </div>
          <div className="modal-body">
            <div className="ticketHistoryGroup">
              <ul className="historyLIst">
                <li className="selected">
                  <strong>Ticket Generated</strong>
                  <p>Generated on 20-03-2023</p>
                </li>
                <li className="selected">
                  <strong>Ticket assigned to 1684</strong>
                  <p>Assigned to Satypal Singh Ranawat (QD2204) on 30-09-24</p>
                </li>
                <li className="selected">
                  <strong>Ticket has been closed</strong>
                  <p>Ticket Closed by Satypal Singh Ranawat (QD2204) on 30-09-24</p>
                  <p> <b> Remark :</b> Lorem Ipsum is simply dummy text of the printing and typesetting
                    industry.
                    Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when
                    an unknown printer took a</p>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default TicketHistoryModal