import React from 'react'

const CreateTicketModal = () => {
  return (
      <div className="offcanvas offcanvas-end creatNewTicketOff" tabIndex={-1} id="creatNewTicket">
      <div className="offcanvas-header">
        <h5 className="offcanvas-title  fs-5 fw-bold text-primary">Create New Ticket</h5>
        <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close" />
      </div>
      <div className="offcanvas-body">
        <div className="formPanel">
          <div className="form-group mb-3">
            <label htmlFor>Help Topic</label>
            <select className="form-select">
              <option value>IT Support Related</option>
              <option value>HR Related</option>
              <option value>Finance Related</option>
              <option value>Admin Related</option>
              <option value>Training Related</option>
            </select>
          </div>
          <div className="form-group mb-3">
            <label htmlFor>Issue Type</label>
            <select className="form-select">
              <option value>Onboarding / New Joining</option>
              <option value>Attendance Related</option>
              <option value>Leave</option>
              <option value>Salary</option>
              <option value>Compliance</option>
              <option value>Grievance</option>
            </select>
          </div>
          <div className="form-group mb-3">
            <label htmlFor>Issue Sub - Type</label>
            <select className="form-select">
              <option value>Punch IN / OUT Issue</option>
              <option value>Correction Issue</option>
              <option value>Attendance Software not working</option>
              <option value>Shift &amp; Time Issue</option>
              <option value>Attendance not marking</option>
              <option value>Mannual Attendance Issue</option>
              <option value>Access card Issue</option>
              <option value>Others (Attendance Related)</option>
            </select>
          </div>
          <div className="form-group mb-3">
            <label htmlFor>Subject</label>
            <textarea className="form-control" placeholder="Enter here.." defaultValue={""} />
          </div>
          <div className="row">
            <div className="col-md-6">
              <div className="form-group mb-3">
                <label htmlFor>Count of User Impacted</label>
                <select className="form-select">
                  <option value>1</option>
                  <option value>2</option>
                  <option value>3</option>
                  <option value>4</option>
                  <option value>5</option>
                  <option value>More than 5</option>
                </select>
              </div>
            </div>
            <div className="col-md-6">
              <div className="form-group mb-3">
                <label htmlFor>Priority Level</label>
                <select className="form-select">
                  <option value>Critical</option>
                  <option value>High</option>
                  <option value>Medium</option>
                  <option value>Low</option>
                </select>
              </div>
            </div>
          </div>
          <div className="form-group mb-3">
            <label htmlFor>Issue Details</label>
            <textarea className="form-control" placeholder="Please Enter Details About Issue" defaultValue={""} />
          </div>
          <div className="d-flex align-items-center justify-content-end gap-2">
            <button type="button" className="btn btn-primary gradientBg">Create Ticket</button>
            <button type="button" className="btn btn-secondary">Reset</button>
          </div>
        </div>
      </div>
    </div>
  )
}

export default CreateTicketModal