import React from 'react'
import CreateTicketModal from '../modal/ticketManagementSystem/CreateTicketModal'
import Button from '../commanComponent/Button'

const CreateNewTicket = () => {
  return (
      <div className="cardBox mb-3 p-0">
      <div className="row align-items-end">
        <div className="col-md-8">
          <div className="cardHead p-3 ">
            <h3 className="mb-2">Welcome To <b className="fs-5">The Support Center</b> Ticket's for everyone.</h3>
            <p className="mb-3">In order to streamline support requests and better serve you, we utilize a
              support ticket system.
              Every support request is assigned a unique ticket number which you can use to track the
              progress
              and responses online. For your reference we provide complete archives and history of all
              your
              support requests. A valid email address is required to submit a ticket.</p>
              <Button btnClassName={"btn btn-primary gradientBg"} dataBsToggle={"offcanvas"} dataBsTarget={"#creatNewTicket"}>Create New Ticket</Button>
            {/* <button type="button" className="btn btn-primary gradientBg" data-bs-toggle="offcanvas" data-bs-target="#creatNewTicket">Create New Ticket</button> */}
          </div>
        </div>
        <div className="col-md-4">
          <figure className="mb-0">
            <img src="images/support-img.svg" alt="" />
          </figure>
        </div>
      </div>
      <CreateTicketModal/>
    </div>
  )
}

export default CreateNewTicket